<template>
  <v-container class="mt-5 pt-5 user-container">
      <v-layout>
          <v-flex xs12 md8 offset-md2>
              <img  src="./../../public/no-person.jpg" class="img-container">
              <h2>{{ user.firstName }} {{ user.lastName }}</h2>
              <p>{{ user.aboutMe }}</p>
                                   <v-card v-if="$store.state.authUser">
                        <v-form
                        @submit.prevent="postComment"
                        class="pa-4"
                        ref="form"
                        v-model="valid"
                        lazy-validation
                    >

                        <v-text-field
                        v-model="comment"
                        :rules="commentRules"
                        label="What's on your mind?"
                        required
                        ></v-text-field>

                        <p v-if="error" class="red--text"> {{ error }} </p>
                        <v-btn
                        :disabled="!valid"
                        class="indigo white--text text-capitalize mt-4"
                        type="submit"
                        >
                        Post A Comment
                        </v-btn>
                    </v-form>
                </v-card>
              <div v-if="comments">
                <v-card v-for="comment in comments" :key="comment.comment" :id="comment.id" class="mt-4 pa-3 animate-fade-in">
                  <h3 class="indigo--text">{{ comment.author }}</h3>
                  <small>{{ comment.date }}</small>
                  <p> {{comment.comment }} </p>
                  <v-btn
                    @click="deleteItem(comment.id)"
                    absolute
                    dark
                    fab
                    top
                    small
                    right
                    color="red accent-3"
                  >
                    <strong>-</strong>
                  </v-btn>
                </v-card>
              </div>
          </v-flex>
      </v-layout>
  </v-container>
</template>


<script>
import db from "@/firebase/init"
import firebase from 'firebase'
import './../../node_modules/nprogress/nprogress.css'
import NProgress from 'nprogress'
import moment from 'moment'


export default {
    data: () => ({
      valid: true,
      comment: '',
      author: '',
      error: null,
      user: null,
      comments: [],
      firstLoad: true,
      commentRules: [
        v => !!v || 'Input is required'
      ]
    }),
  methods: {
      postComment() {
    NProgress.start()
    if (this.$refs.form.validate()) {
          this.firstLoad = false
          db.collection('users').doc(this.$route.params.curr_id).get().then(doc => {
          let user = doc.data()
          this.author = `${user.firstName} ${user.lastName}`
          this.date = moment(String(Date())).format('MM/DD/YYYY hh:mm')
        }).then(() => {
          db.collection('users').doc(this.$route.params.curr_id).collection('comments').add({author: this.author, comment: this.comment, date: this.date })
          this.comment = ' '
          NProgress.done()
        })
      }
      
    },

    deleteItem(commentID) {
      let currentCont = document.getElementById(commentID)
      currentCont.classList.remove('animate-fade-in')
      currentCont.classList.add('animate-fade-out')
      setTimeout(() => {
        currentCont.style.display = 'none'
        db.collection('users').doc(this.user.id).collection('comments').doc(commentID).delete()
      }, 500)
    }
  },
   created () {
     NProgress.start()
     if(!this.$store.state.users) {
     db.collection('users').doc(this.$route.params.curr_id).get().then(doc => {
       this.user = doc.data()
       NProgress.done()
     })
    } else {
      this.$store.state.users.forEach(user => {
          if (user.id == this.$route.params.curr_id) {
            this.user = user
            NProgress.done()
          }
        })
      } 
      
      db.collection('users').doc(this.$route.params.curr_id).collection('comments').onSnapshot(snapshot => {
        snapshot.docChanges().forEach(change => {
          if(change.type == 'added' && !this.firstLoad) {
          let comment = change.doc.data()
          comment.id = change.doc.id
          this.comments.unshift(comment)
          
          }
        }) 
      })
      if(this.firstLoad) {
        db.collection('users').doc(this.$route.params.curr_id).collection('comments').orderBy('date', 'desc').get().then(querySnap => {
        querySnap.forEach(doc => {
          let comment = doc.data()
          comment.id = doc.id
          this.comments.push(comment)
          this.firstLoad = false
        }) 
      })
      }
         
  },

      beforeRouteEnter (to, from, next) {
        next(vm => {
          if(vm.$route.params.curr_id != firebase.auth().currentUser.uid) vm.$router.push('/users')
        })
      }
    }
</script>


<style lang="sass">

.animate-fade-in
  animation: comment-fade-in .7s forwards ease-in-out

@keyframes comment-fade-in 
  from
    opacity: 0
    transform: scale(0)

  to
    opacity: 1
    transform: scale(1)


.user-container h2
    color: #3f51b5 !important

.user-container p
    color: #3f51b5 !important

.user-container img
    color: #fff !important

.user-container a
    position: relative
    right: 10px

.user-container .image-container
  width: 100px
  height: 100px
  background-color: black
  margin-left: 0
  margin-bottom: 20px

.animate-fade-out
  animation: comment-fade-out .5s forwards ease-in-out

@keyframes comment-fade-out 
  from
    opacity: 1
    transform: scale(1)

  to
    opacity: 0
    transform: scale(0)

</style>
